<h1 id="peter-s-feedback" tabindex="-1"><a class="header-anchor" href="#peter-s-feedback" aria-hidden="true">#</a> Peter's Feedback</h1>
<p>On this page you find instructions on how to interpret feedback on your work received from me (Peter).</p>
<p>Getting feedback on your work is very important for you to understand what you can do to improve it and get better at the subject. That's why we recommend you to get continuos feedback on your work throughout the entire course by discussing it with a teacher at the lab sessions.</p>
<p>When marking the final submission of your work we need to read through and assess your work. Marking your work is not a <em>teaching activity</em>, but part of an <em>examination test</em>, so our feedback on your final submissions are not intended for you, but instead something we use ourselves to justify the grade we give you. But since it's also very valuable for you as a student to read it, I often share my feedback with you, so you can learn what you can improve. Since the feedback primarily is for me, <strong>my feedback on your work might be hard for you to read, and it will likely overall be incomplete</strong>.</p>
<h2 id="report-feedback" tabindex="-1"><a class="header-anchor" href="#report-feedback" aria-hidden="true">#</a> Report Feedback</h2>
<p>If you've submitted a report (PDF file), I will write my feedback as PDF comments in it. The yellow lines in the report indicates where I've commented on something, and you can usually read the comments by hovering the mouse over the line or clicking on it.</p>
<div class="custom-container warning"><p class="custom-container-title">A proper PDF Reader might be required</p>
<p>Many web browsers can display PDF files, and some even displays where the places comments have been written in it (the yellow lines), but not all of them can display the actual comments. For that you might need a proper PDF Reader, such as <a href="https://get.adobe.com/reader/" target="_blank" rel="noopener noreferrer">Adobe Acrobat Reader DC<OutboundLink/></a> (free to download and install).</p>
</div>
<p>I try to avoid repeating comments on the same type of errors, so if I have complained about something in the beginning of the report (for example that you are using acronyms wrong), then you need to look for and fix those type of errors in the rest of the report yourself.</p>
<p>There are probably more things in the report that can be improved than I have time to comment on, so for bad reports that can be extensively improved I tend to only comment on the biggest mistakes, and for pretty good reports that can't be improved that much I can only comment on minor things, which some times are not necessarily wrong, but might rather be my personal opinion. I also tend to only comment on the things you have written, and not so much on all the things you should have written, but haven't.</p>
<p>Whenever I think the quality of your report is too poor to make you receive a passing grade on your entire project, I'll stop reading and marking the rest of your work. Your grade has already been decided, and looking through your submitted code (if any) won't change that, so no point for me in doing that.</p>
<h2 id="code-feedback" tabindex="-1"><a class="header-anchor" href="#code-feedback" aria-hidden="true">#</a> Code Feedback</h2>
<p>If your report is good enough to have a chance to pass, I'll start reading the code you have submitted (if any). I'll read through <strong>parts of it</strong> and comment anything I find suspicious/bad/incorrect. You'll later receive these comments. If I find too severe mistakes preventing you from getting a passing grade I usually stop looking for more mistakes, so <strong>students that need to improve their work often need to improve more things than just the things I've commented on</strong>.</p>
<div class="custom-container warning"><p class="custom-container-title">Getting code feedback does not mean the report is good</p>
<p>If you receive comments on your code, that does not necessarily mean your report is good. It (in most case) means that the poorer parts of your report can be outweighed by high quality code, and you still had a chance to get a passing grade in total. If you didn't get that, you are strongly recommended to improve both the report and your code for next revision.</p>
</div>
<h2 id="final-grade" tabindex="-1"><a class="header-anchor" href="#final-grade" aria-hidden="true">#</a> Final Grade</h2>
<p>The final grade I give you on your work is not only based on my written feedback. While looking through your work I'll also assess your work per the grading guidelines (available in most of my courses), and I'll also assess how well your work is overall. All of this is then taken into account for the final grade.</p>
<h2 id="revisions" tabindex="-1"><a class="header-anchor" href="#revisions" aria-hidden="true">#</a> Revisions</h2>
<p>The students who need to improve their work to get a passing grade often wants to get <em>a minimal list of things to fix to get a passing grade</em>. My feedback is no such list. The closest you'll get to such a list is by reading through the project instructions, the hinting text in the project report template and the project grading guidelines. Together they should give you a very good understanding of what's required to receive a passing grade.</p>
<p>It might not be necessary for you to fix all the things I've commented on to get a passing grade. When you submit your revision I'll make a new assessment of your entire work from scratch. This time it might overall be good enough to get a passing grade, even if you just improved the report, or just improved the code. I strongly recommend you to fix all the things I've commented on, but if you want to gamble it's up to you.</p>
<div class="custom-container warning"><p class="custom-container-title">I'll miss commenting on some bad things</p>
<p>When I read through a revision I might notice mistakes/errors that were there already in the first submission, but I never commented on. For the student, that <strong>does not mean</strong> <em>I don't need to fix that since you didn't comment on it before, so I had no idea that it was something I needed to fix</em>. To learn what you need to deliver to pass, read through the project instructions, the project grading guidelines and the hints in the report template. My feedback is incomplete, since I'll miss commenting on some things, and anyway don't have time to comment on all things.</p>
</div>
<p>Many students often submit revisions 1-2 hours after they've received my feedback. Most likely it will take more than 1-2 hours to improve your work to make it good enough to receive a passing grade. These students often also want me to mark their submissions as soon as they have submitted their revisions. That will not happen. The deadlines for submitting revisions are at the same time as for the re-exam of the written examination in my courses (if not stated otherwise), and that's when I'll mark your submitted revisions.</p>
<p>The reason we only mark your work at specific deadlines is to avoid students submitting revisions with small changes again, and again, and again, until it's good enough to pass. It takes too much time for us teachers to mark revisions. By only marking your work at specific deadlines we:</p>
<ul>
<li>Encourage students to submit proper work (since most of them don't want to wait until the next submission occasion to receive a passing grade)</li>
<li>Avoid wasting time on marking unnecessary many revisions</li>
</ul>
